*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: #0f1f38;
  background-color: #0f1f38;
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 14px;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  background-color: #0f1f38;
}

body::-webkit-scrollbar-thumb {
  background-color: #034466;
  border: 1px solid #034466;
  border-radius: 6px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  margin: 0;
  font-style: normal;
  text-decoration: none;
}

button {
  cursor: pointer;
  padding: 0;
}

#particles-js {
  pointer-events: none;
  z-index: -5;
  opacity: .65;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
}

@media screen and (min-width: 768px) {
  #particles-js {
    opacity: .85;
  }
}

@media screen and (min-width: 1280px) {
  #particles-js {
    opacity: .95;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.section {
  padding: 100px 0 20px;
}

@media screen and (min-width: 768px) {
  .section {
    padding: 110px 0 0;
  }
}

@media screen and (min-width: 1280px) {
  .section {
    padding: 120px 0 20px;
  }
}

.header {
  width: 100%;
  z-index: 1;
  min-height: 90px;
  background-color: #0000;
  justify-content: space-between;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.header-scroll {
  backdrop-filter: blur(10px);
  background-color: #ffffff0d;
}

.header__container {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.logo__header {
  transform: rotate(0);
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.logo__header:hover, .logo__header:focus {
  transform: rotate(-25deg);
}

.header__list {
  align-items: center;
  display: flex;
}

.header-nav__list {
  display: none;
}

@media screen and (min-width: 1280px) {
  .header-nav__list {
    justify-content: space-between;
    align-items: center;
    gap: 25px;
    margin-left: 400px;
    display: flex;
  }
}

.header-nav__link {
  color: #8e7970;
  padding: 20px 0;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
}

@media screen and (min-width: 1280px) {
  .header-nav__link:after {
    content: "";
    min-width: 100%;
    height: 4px;
    background-color: #0000;
    border-radius: 2px;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    bottom: 5px;
    left: 0;
    transform: scaleX(0);
  }
}

@media screen and (min-width: 1280px) {
  .header-nav__link:hover:after, .header-nav__link--current:after {
    content: "";
    min-width: 100%;
    height: 4px;
    background-color: #f55449;
    border-radius: 2px;
    position: absolute;
    bottom: 5px;
    left: 0;
    transform: scaleX(1);
  }
}

.header-nav__link--current, .header-nav__link:hover {
  color: #f55449;
}

.header-contact__list {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-contact__list {
    justify-content: center;
    align-items: center;
    gap: 25px;
    margin-left: auto;
    display: flex;
  }
}

.header-contact__link {
  cursor: pointer;
}

.header-contact__icon {
  fill: #034466;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.header-contact__link .header-contact__icon:hover, .header-contact__link .header-contact__icon:focus {
  fill: #f55449;
}

.hero {
  padding: 0;
}

@media screen and (min-width: 768px) {
  .hero {
    margin-top: -20px;
  }
}

@media screen and (min-width: 1280px) {
  .hero {
    margin-top: -120px;
  }
}

#copy {
  display: none;
}

canvas {
  pointer-events: none;
  max-width: 100%;
  height: auto;
}

@media screen and (min-width: 480px) {
  canvas {
    width: 100%;
  }
}

.hero__animation {
  touch-action: none;
  justify-content: center;
  display: flex;
}

.hero__info {
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: -50px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .hero__info {
    flex-direction: row;
    justify-content: flex-start;
    gap: 20px;
    margin-top: -170px;
  }
}

@media screen and (min-width: 1280px) {
  .hero__info {
    gap: 150px;
    margin-top: -320px;
  }
}

.hero__img {
  max-width: 100%;
}

@media screen and (min-width: 768px) {
  .hero__img {
    max-width: 450px;
    height: auto;
  }
}

@media screen and (min-width: 1280px) {
  .hero__img {
    max-width: 600px;
  }
}

.hero-box {
  text-align: center;
  width: 240px;
  padding: 20px 0;
}

@media screen and (min-width: 768px) {
  .hero-box {
    text-align: start;
    width: 240px;
    padding: 80px 0;
  }
}

@media screen and (min-width: 1280px) {
  .hero-box {
    width: 350px;
    padding: 80px 0;
  }
}

.hero-box__text {
  color: #8e7970;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
}

@media screen and (min-width: 768px) {
  .hero-box__text {
    margin-bottom: 20px;
    margin-left: -20px;
  }
}

@media screen and (min-width: 1280px) {
  .hero-box__text {
    margin-bottom: 20px;
    margin-left: -40px;
    font-size: 42px;
    font-weight: 500;
    line-height: 48px;
  }
}

.hero-box__title {
  color: #f55449;
  background-color: #034466;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  margin-bottom: 15px;
  padding: 5px 10px;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
}

@media screen and (min-width: 1280px) {
  .hero-box__title {
    font-size: 36px;
    font-weight: 500;
    line-height: 42px;
  }
}

.hero-box__wrapper {
  justify-content: center;
  display: flex;
}

.hero-box__subtitle {
  width: 20ch;
  color: #8e7970;
  white-space: nowrap;
  border-right: 4px solid #f55449;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
  animation: expand-contract 15s steps(20, end) infinite, flashin-border .75s step-start infinite;
  overflow: hidden;
}

@media screen and (min-width: 1280px) {
  .hero-box__subtitle {
    font-size: 36px;
    font-weight: 500;
    line-height: 42px;
  }
}

@keyframes flashin-border {
  0%, 100% {
    border-color: #f55449;
  }

  50% {
    border-color: #0000;
  }
}

@keyframes expand-contract {
  0%, 100% {
    width: 0;
  }

  25%, 75% {
    width: 20ch;
  }
}

.hero-resume {
  width: 140px;
  height: 40px;
  text-align: center;
  background-color: #0000;
  border: 2px solid #8e7970;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 0 auto;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (min-width: 768px) {
  .hero-resume {
    margin: 0 0 0 auto;
  }
}

.hero-resume:hover, .hero-resume:focus {
  border: 2px solid #f55449;
}

.hero-resume__icon {
  fill: #8e7970;
}

.hero-resume__text {
  color: #8e7970;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}

.hero-title {
  height: 800px;
  color: wheat;
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle--open {
  margin-left: auto;
}

@media screen and (min-width: 768px) {
  .menu-toggle--open {
    margin-left: 450px;
  }
}

.menu-toggle__icon {
  fill: #8e7970;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.menu-toggle:hover .menu-toggle__icon, .menu-toggle:focus .menu-toggle__icon {
  fill: #f55449;
}

.menu-container {
  width: 230px;
  height: 430px;
  visibility: hidden;
  opacity: 0;
  z-index: 1;
  background-color: #034466;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  padding: 48px 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .menu-container {
    height: 360px;
  }
}

.menu-container.is-open {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.menu-toggle--close {
  position: absolute;
  top: 25px;
  right: 15px;
}

.mobile-menu {
  flex-direction: column;
  display: flex;
}

.mobile-menu__nav {
  margin-bottom: auto;
}

.mobile-menu__item:not(:last-child) {
  margin-bottom: 20px;
}

.mobile-menu__link {
  color: #8e7970;
  font-size: 30px;
  font-weight: 500;
  line-height: 36px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.mobile-menu__link:hover, .mobile-menu__link:focus, .mobile-menu__link--current {
  color: #f55449;
}

.mobile-contact__list {
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin-top: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .mobile-contact__list {
    display: none;
  }
}

.mobile-contact__link {
  cursor: pointer;
}

.mobile-contact__icon {
  fill: #0f1f38;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.mobile-contact__link .mobile-contact__icon:hover, .mobile-contact__link .mobile-contact__icon:focus {
  fill: #f55449;
}

.skills__title, .projects__title, .about__title, .contact__title {
  color: #8e7970;
  text-align: center;
  margin-bottom: 250px;
  font-size: 30px;
  font-weight: 600;
  line-height: 36px;
}

@media screen and (min-width: 768px) {
  .skills__title, .projects__title, .about__title, .contact__title {
    margin-bottom: 60px;
  }
}

@media screen and (min-width: 1280px) {
  .skills__title, .projects__title, .about__title, .contact__title {
    margin-bottom: 80px;
    font-size: 42px;
    font-weight: 600;
    line-height: 48px;
  }
}

.projects__title, .about__title, .contact__title {
  margin-bottom: 40px;
}

.skills__title:before, .projects__title:before, .about__title:before, .contact__title:before {
  content: "";
  width: 15px;
  height: 15px;
  border-bottom: 3px solid #8e7970;
  border-right: 3px solid #8e7970;
  margin-right: 15px;
  display: inline-block;
  transform: rotate(-45deg)skew(15deg, 15deg);
}

@media screen and (min-width: 768px) {
  .skills__title:before, .projects__title:before, .about__title:before, .contact__title:before {
    width: 18px;
    height: 18px;
    margin-right: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .skills__title:before, .projects__title:before, .about__title:before, .contact__title:before {
    width: 25px;
    height: 25px;
    border-bottom: 4px solid #8e7970;
    border-right: 4px solid #8e7970;
  }
}

@media screen and (max-width: 767px) {
  .skills__box {
    margin: 0 -200px;
    transform: rotate(90deg);
  }
}

.skills-boxes {
  visibility: hidden;
  opacity: 0;
  transition: transform 1s cubic-bezier(.4, 0, .2, 1), visibility 1s cubic-bezier(.4, 0, .2, 1), opacity 1s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(150%);
}

.skills-boxes.show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.skills-boxeses {
  visibility: hidden;
  opacity: 0;
  transition: transform 1.5s cubic-bezier(.4, 0, .2, 1), visibility 1.5s cubic-bezier(.4, 0, .2, 1), opacity 1.5s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(80%);
}

.skills-boxeses.show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.skills__wrapper {
  justify-content: center;
  align-items: center;
  gap: 45px 35px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .skills__wrapper {
    gap: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .skills__wrapper {
    gap: 65px;
  }
}

.skills-box {
  width: 75px;
  height: 75px;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  transform: rotate(45deg);
}

@media screen and (min-width: 768px) {
  .skills-box {
    width: 75px;
    height: 75px;
  }
}

@media screen and (min-width: 1280px) {
  .skills-box {
    width: 120px;
    height: 120px;
  }
}

.skills-box:hover, .skills-box:focus {
  transform: scale(1.1)rotate(45deg);
  box-shadow: 0 0 20px #f55449;
}

.skills-box__border {
  width: 100%;
  height: 100%;
  background-color: #0f1f38;
  border-radius: 10px;
  position: relative;
}

.skills-box__border:after {
  content: "";
  z-index: -1;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  background: linear-gradient(60deg, #0f1f38, #f55449, #034466, #f55449) 0 / 300% 300%;
  border-radius: 10px;
  animation: moveGradient 4s infinite alternate;
  position: absolute;
  top: -2px;
  left: -2px;
}

@keyframes moveGradient {
  50% {
    background-position: 100%;
  }
}

.skills-box__border {
  justify-content: center;
  align-items: center;
  display: flex;
}

.skills-box__img {
  width: 50px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: rotate(-135deg);
}

@media screen and (min-width: 768px) {
  .skills-box__img {
    transform: rotate(-45deg);
  }
}

@media screen and (min-width: 1280px) {
  .skills-box__img {
    width: 90px;
  }
}

.skills-box:hover .skills-box__img, .skills-box:focus .skills-box__img {
  transform: scale(.85)rotate(-135deg);
}

@media screen and (min-width: 768px) {
  .skills-box:hover .skills-box__img, .skills-box:focus .skills-box__img {
    transform: scale(.85)rotate(-45deg);
  }
}

